.sidebarOption {
  display: flex;
  align-items: center;
  font-size: 12px;
  cursor: pointer;
  background-color: var(--slack-color);
  max-width: 260px;
  flex: 0.3;
}

.sidebarOption:hover {
  background-color: #340e36;
  opacity: 0.9;
}

.sidebarOption__icon {
  padding: 10px;
  font-size: 15px !important;
}
.sidebarOption__channel {
  padding: 10px 0;
}

.sidebarOption__hash {
  padding: 10px;
}

.sidebarOption > h3 {
  font-weight: 500;
}
